(() => {
  let currentMode = '3D';
  
  const toggleBtn = document.getElementById('heatmap-toggle');
  if (!toggleBtn) {
    console.error('âŒ æ‰¾ä¸åˆ°åˆ‡æ¢æŒ‰é’®');
    return;
  }
  
  const container3D = document.getElementById('heatmap-3d');
  const container2D = document.getElementById('heatmap-2d');
  
  if (!container3D || !container2D) {
    console.error('âŒ æ‰¾ä¸åˆ°çƒ­åŠ›å›¾å®¹å™¨');
    return;
  }
  
  function updateButtonText() {
    const toggleText = toggleBtn.querySelector('.toggle-text');
    if (toggleText) {
      toggleText.textContent = currentMode;
    }
  }
  
  function toggleMode() {
    if (currentMode === '3D') {
      currentMode = '2D';
      container3D.style.display = 'none';
      container2D.style.display = 'block';
    } else {
      currentMode = '3D';
      container2D.style.display = 'none';
      container3D.style.display = 'block';
    }
    updateButtonText();
    console.log(`âœ… åˆ‡æ¢åˆ°${currentMode}æ¨¡å¼`);
  }
  
  toggleBtn.addEventListener('click', toggleMode);
  updateButtonText();
  
  // ç¡®ä¿åˆå§‹çŠ¶æ€æ­£ç¡®
  container3D.style.display = 'block';
  container2D.style.display = 'none';
  
  console.log('âœ… çƒ­åŠ›å›¾åˆ‡æ¢æ§åˆ¶å™¨åˆå§‹åŒ–å®Œæˆ');
})();
